import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'
Vue.use(Vuex)
Vue.prototype.$axios = axios;
export default new Vuex.Store({
  state: {
    albumImgUrl: "",
    singerAvatarUrl: "",
    songName: "",
    singerName: "",
    songUrl: "",
    isplay: true,
    progressNum: 0,
    frameId: 0,
    rotateframeId: 0,
    LrcArr: [],
    i: 0,
    nowObj:{}
  },
  mutations: {
    SongInfo(state, res) {
      // console.log(state.songName)
    },
    SongInfo(state, res) {
      state.songUrl = res
      // document.getElementById("audio").play()
      // console.log(state.songUrl)
    },
    change(state, obj) {
      state.progressNum = obj.progress
      state.frameId = obj.id
    },
    nowObj(state, obj) {
      state.nowObj = obj;
      console.log(obj)
    }
  },
  actions: {
    GetSongInfo(context, Song) {
        //æ­Œæ›²å¤´åƒï¼Œåï¼Œæ­Œæ‰‹
        let str = "https://music.niubishanshan.top/api/v2/music/albumImg/" + Song.albumMid + "/" + Song.singer[0].singerMid + "/"
        axios.get(str)
          .then((res) => {
            context.state.songName = Song.songName
            context.state.singerName = Song.singer[0].singerName
            context.state.albumImgUrl = res.data.data.albumImgUrl
            context.state.singerAvatarUrl = res.data.data.singerAvatarUrl
            // console.log(Song)
            context.commit('SongInfo', res.data)
          })
          .catch(function (error) {
            
          })

      
    },
    GetSongUrl(context, Song) {
        console.log(Song)
        let str2 = "https://music.niubishanshan.top/api/v2/music/songUrllist/" + Song.songMid
        axios.get(str2)
          .then((res) => {
            context.commit('SongInfo', res.data.data)
          })
          .catch(function (error) {
          })
    },

    GetLrc(context, Song) {
      // console.log(Song.songId)
      let str3 = "https://music.niubishanshan.top/api/v2/music/lrc/" + Song.songId
      axios.get(str3)
        .then((res) => {
          // console.log(res.data.data.lyric)
          context.state.Lrc = res.data.data.lyric.replace(/\[æ¢è¡Œ\]/g, '$')
          var arr = context.state.Lrc.split("$")
          let tempObj,
              time,
              temp,
              text,
            index
          context.state.LrcArr = []
          for (let i = 0; i < arr.length; i++){
            if (i >= 5) {
              index = arr[i].indexOf("]");
              text =arr[i].slice(index + 1,arr.length)
              temp = arr[i].slice(0, index + 1).replace(/\[/g, '').replace(/\]/g, '').split(":")
              time =temp[0] * 60 + temp[1] * 1
              tempObj = { time, text }
              
              context.state.LrcArr.push(tempObj);
            }
          }
          console.log(context.state.LrcArr)
        })
        .catch(function (error) {})
    }

  }
})
