<template>
   <div id='miniPlayer'>
       
       <div class="wrap">
           <router-link to="/lrc:id">
            <div class="img">
                <img :src="albumImgUrl" alt="" ref="img" id="rotateimg">
            </div>
            <audio :src="songUrl" ref="audio" autoplay id="audio">
            </audio>
            <p class="info" ><span>{{songName}}</span>- <span>{{singerName}}</span></p>
            </router-link>
            <div class="contral" @click.stop="toggle">
                <span class="iconfont" v-if="this.$store.state.isplay">&#xe629;</span>
                <span class="iconfont" v-if="!this.$store.state.isplay">&#xe606;</span>
            </div>
            
            <div class="slider" @click="progress" id="slider" ref="slider" style="width:100%">
                <div class="progress" ref="progress" id="progress" >
                    <div class="bar" ref="bar" id="bar"></div>
                </div>
            </div>
       </div>
   </div>
</template>

<script>
import { setInterval } from 'timers';
export default {
    name:'',
    data(){
        return{
            show:false,
            isPlay:false,
            percent:0,
            duration:0,
            currentTime:0,
            rotate:0,
            frameId:0,
            nowCurrentTime:0
        }
    },
    computed:{
        albumImgUrl(){
            if(this.$store.state.songName!=""){
                return this.$store.state.albumImgUrl
            }else{
                return "https://y.gtimg.cn/music/common/upload/t_playsong_ad/1207759.png?max_age=2592000"
            }
        },
        
        songUrl(){
            return this.$store.state.songUrl
        },
        songName(){
            if(this.$store.state.songName!=""){
                return this.$store.state.songName
            }
            return "ä½ è¿˜æ²¡æœ‰é€‰æ‹©æ­Œæ›²"
        },
        singerName(){
            if(this.$store.state.songUrl!=""){
                return this.$store.state.singerName
            }
            return ""
        }
    },
    methods:{
        toggle(){
            console.log(this.songUrl)
            if(this.$store.state.isplay==false){
                this.$refs.audio.play()
                this.$store.state.isplay=!this.$store.state.isplay;
                var _this=this
                function frame(){
                    _this.frameId=_this.$store.state.rotateframeId
                    _this.rotate+=0.25
                    _this.$refs.img.style.transform=`translate(0,-70%) rotateZ(${_this.rotate}deg)`
                    _this.frameId=requestAnimationFrame(frame)

                }
                frame();

            }else{
                cancelAnimationFrame(this.frameId)
                this.$refs.audio.pause()
                this.$store.state.isplay=!this.$store.state.isplay;
            }
            
        },
        progress(){
            let progress=this.$refs.progress;
            let slider=this.$refs.slider;
            let audio=this.$refs.audio;
            var currentTime=audio.currentTime
            let width = slider.clientWidth
            let anchor=Math.round(event.x/width*100)/100;
            audio.currentTime=audio.duration*anchor   
        }
    },
    watch:{
        songUrl(){
            cancelAnimationFrame(this.frameId)
            var _this=this
            function frame(){
                _this.frameId=_this.$store.state.rotateframeId
                _this.rotate+=0.25
                _this.$refs.img.style.transform=`translate(0,-70%) rotateZ(${_this.rotate}deg)`
                _this.frameId=requestAnimationFrame(frame)
            }
            frame();
        },
        
    },
    created(){
        
    }
}
</script>

<style scoped>
    .slider{
        position: absolute;
        bottom: 0.5px;
        height: 3px;
        background: rgba(0, 0, 0, 0.164);
        opacity: 1;
    }
    .progress{
        width: 0%;
        height: 100%;
        background: #31c27c;
        position: relative;
    }
    .bar{
        width: 7px;
        height: 7px;
        background: rgba(2, 2, 2, 0.514);
        border-radius: 50%;
        position: absolute;
        right: 0;
        top: 50%;
        transform: translateY(-50%);
        opacity: 0;
    }
    #miniPlayer{
        width: 100%;
        height: 40px;
        background: #fff;
        position: fixed;
        bottom: 0;
         box-shadow: 0 5px 5px rgba(0,0,0,.02);
         border-top: 1px solid rgba(0, 0, 0, 0.13);
         /* border-bottom: 1px solid #000; */
    }
    .wrap{
        width: 100%;
        height: 100%;
        position: relative;
    }
    .info{
        padding: 10px 0 10px 90px;
        letter-spacing: 1px;
        color: rgba(0, 0, 0, 0.63);
        font-size: 12px;
        display: inline-block;
        overflow: hidden;
        width: 60%;
        white-space:nowrap

    }
    .img img{
        width: 45px;
        border-radius: 50%;
        position: absolute;
        left: 20px;
        top: 50%;
        transform: translate(0,-70%);
        box-shadow: 0 7px 13px rgba(0,0,0,.21);
    }
    .contral{
        display: inline-block;
        position: absolute;
        right: 20px;
        top: 50%;
        transform: translate(0,-50%)
    }
    .contral span{
       font-size: 25px;
       color: #31c27c;
    }
</style>