import Vue from 'vue'
import Router from 'vue-router'
import Rank from './components/Rank.vue'
import Recommend from './components/Recommend.vue'
import Search from './components/Search.vue'
import Home from './components/Home.vue'
import SongRank from './components/SongRank.vue'
import Song from './components/Song.vue'
import Singer from './components/Singer.vue'
import Lrc from './components/Lrc.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: '',
      redirect:"/home"
    },
    {
      path: '/home',
      component: Home,
      children: [
        {
          path: '/',
          name: '',
          redirect: "/recommend"
        },
        {
          path: '/rank',
          name: 'Rank',
          component: Rank,
          children: [
              
          ]
        },
        {
          path: '/recommend',
          name: 'Recommend',
          component: Recommend
        },
        {
          path: '/search',
          name: 'Search',
          component: Search
        },
      ]
    },
    {
      path: '/songrank/:id',
      name: 'songrank',
      component: SongRank
    },
    {
      path: '/song/:val',
      name: 'song',
      component: Song
    },
    {
      path: '/singer/:val',
      name: '',
      component: Singer
    },
    {
      path: '/lrc:id',
      name: '',
      component: Lrc
    },

  ]
})
