<template>
   <div id='Search'>
       <div class="inputwrap">
           <span class="iconfont search">&#xe62b;</span>
           <input v-model="inputvalue" type="text" class="input" @keyup="ShowKey"  @focus="ShowSpan" ref="input" placeholder="æœç´¢æ­Œæ›²ï¼Œæ­Œå•ï¼Œä¸“è¾‘">
           <span class="iconfont close" v-show="val" ref="delete" @click="deleteVal">&#xe63a;</span>
           <span v-show="show" class="showspan" @click="hiddeSpan" ref="close">å–æ¶ˆ</span>
        </div>
        <ul class="songlist">
                <router-link :to="'/singer/'+inputvalue" tag="li" v-if="issingername" class="singerTop song">
                <span><img :src="singerURL" alt=""></span>
                
                <div class="info"> 
                    <p class="singer-info">{{this.inputvalue}}</p>
                    <p class="singer">æ­Œæ‰‹</p>
                </div>
                </router-link>
            
            <li class="song" v-for="(item,index) in data.songList" :key="index" @click="play(item)">
                <span class="iconfont music">&#xe618;</span>
                <div class="info"> 
                    <p class="song-name">{{item.songName}}</p>
                    <p class="singer">
                         <span v-for="(singer,index) in item.singer" :key="index">
                            {{singer.singerName}} 
                        </span>
                    </p>
                </div>
            </li>
            <router-link :to="'/'+u+'/'+inputvalue" tag="li" class="link" v-if="val">æŸ¥çœ‹æ›´å¤š</router-link>
            
        </ul>
   </div>
</template>

<script>
import { setTimeout, clearTimeout } from 'timers';
   export default {
       name:'',
       data(){
            return{
               show:false,
               val:false,
               data:{},
               singerData:{},
               inputvalue:'',
               setTimeOutId:null,
               issingername:false,
               singerURL:"",
               u:"song"
            }
       },
       methods:{
           ShowSpan(){
               this.$refs.delete.style.right="75px"
               event.target.style.width="85%"
               this.show=true;
           },
           updatainput(){
               if(this.$refs.input.value!=""){

                   this.id=this.$route.params.id;
                   let str='https://music.niubishanshan.top/api/v2/music/search/'+this.inputvalue+'/1/5';
                   this.$axios.get(str)
                    .then((res)=>{
                        this.data=res.data.data;
                        console.log(this.data)
                        this.singer()
                        if( this.issingername){
                            this.$axios.get(str)
                        }
                    })
                    .catch(function (error) {
                        console.log(error);
                    });
                }else{
                    this.issingername=false
                    this.data={}
                }
           },
           ShowKey(){
                this.val=true
               clearTimeout(this.setTimeOutId)
               this.setTimeOutId=setTimeout(()=>{
                   this.updatainput();
               },1000)
           },
           hiddeSpan(){
               this.$refs.delete.style.right="23px"
               this.$refs.input.style.width="100%"
               this.$refs.delete.style.right="23px"
               this.show=false;
               this.data={},
               this.inputvalue=""
               this.val=false
            },
            deleteVal(){
                this.$refs.input.value=""
                this.val=false
            },
            singer(){
                let a,b;
                for (let i = 0; i < this.data.songList.length; i++) {
                    let arr1=this.data.songList[i].singer;
                    for (let j = 0; j < arr1.length; j++) {

                       if(this.inputvalue==arr1[j].singerName){
                           this.issingername=true;
                           this.u="singer"
                            a=this.data.songList[i].albumMid;
                            b=arr1[j].singerMid;
                       }
                       else{
                           this.issingername=false;
                       }
                    }
                }
                let str="https://music.niubishanshan.top/api/v2/music/albumImg/"+a+"/"+b+"/"
                this.$axios.get(str)
                    .then((res)=>{
                        this.singerURL=res.data.data.singerAvatarUrl
                    })
                    .catch(function (error) {
                        console.log(error);
                    });
            },
            play(data){
                this.$store.state.isplay=true
                var audio= document.getElementById("audio")
                audio.currentTime=0
                var slider= document.getElementById("slider")
                var progress= document.getElementById("progress")
                // console.log(data)
                this.$store.dispatch('GetSongInfo',{
                    albumMid:data.albumMid,
                    singer:data.singer,
                    songId: data.songId,
                    songMid: data.songMid,
                    songName: data.songName
                })
                this.$store.dispatch('GetSongUrl',{
                    albumMid:data.albumMid,
                    singer:data.singer,
                    songId: data.songId,
                    songMid: data.songMid,
                    songName: data.songName
                })
                var progressNum,id,_this=this;
                setTimeout(()=>{
                   
                    frame()
                },500)
                function frame(){
                    var duration=audio.duration
                    var currentTime=audio.currentTime
                    
                    progressNum=currentTime/duration*100
                    if(progressNum<100){
                        progress.style.width=progressNum+"%"
                        id=requestAnimationFrame(frame)
                        _this.$store.commit("change",{progressNum,id})
                    }else{
                        cancelAnimationFrame(id)
                    } 
                }
            }
        }
   }
</script>

<style scoped>
    .link{
        width: 100%;
        border-top: 1px solid rgb(229,229,229);
        padding: 7px 10px;
        box-sizing: border-box;
        display: block;
        color: #000;
        text-align: center;
    }
    .singerTop .info{
        padding-left: 10px;
    }
    .singerTop{
        padding: 5px 10px;
        box-sizing: border-box;
    } 
    .singerTop img{
        width: 40px;
        border-radius: 50%;
    }
    .songlist{
        width: 100%;
        background: #fff;
    }
    .song{
        width: 100%;
        border-top: 1px solid rgb(229,229,229);
        padding: 7px 10px;
        display: flex;
        box-sizing: border-box;
    }
    .info{
        display: inline-block;
        flex: 8
    }
     .info .song-name{
         font-size: 16px;
     }
     .info .singer{
         font-size: 12px;
         color:grey
     }
    .music{
        flex: 1;
        display: inline-block;
        padding-left: 12px;
        line-height: 42px;
        font-size: 22px;
        color: rgba(0, 0, 0, .3);
    }

    .inputwrap{
        width: 100%;
        padding: 10px;
        box-sizing: border-box;
        background: #f4f4f4;
        position: relative;
    }
    input{
        width: 100%;
        padding: 10px 1px 10px 40px;
        box-sizing: border-box;
        outline: none;
        border-radius: 4px;
        box-shadow: none;
        border: 1px solid #eee;
        color:rgba(0, 0, 0, .3);
    }
    input::-webkit-input-placeholder {
        /* placeholderé¢œè‰²  */
        color: rgba(0, 0, 0, .3);
        /* placeholderå­—ä½“å¤§å°  */
        font-size: 14px;
    }
    .search{
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        left: 25px;
        transform: translateY(-55%);
        font-weight: 700;
        color: rgba(0, 0, 0, .3);
    }
    .close{
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        right: 23px;
        color: rgba(0, 0, 0, 0.349);
    }
    .showspan{
        display: inline-block;
        padding: 0 10px;
        font-size: 14px;
        color: rgba(0, 0, 0, 0.616);
        box-sizing: border-box;
    }

</style>