<template>
   <div id='rank' v-if="a">
        <div class="rankList" v-for="(item,index) in data" :key="index">
            <router-link :to="'/songrank/'+item.id">
                <div class="rank-info">
                    <div class="img">
                        <img :src="item.picUrl" class="jp" alt="">
                        <span class="count">{{item.listenCount|fliterNum}}</span>
                        </div>
                    <div class="song-rank">
                        <div class="box">
                            <h3>{{item.title}}</h3>
                            <ul>
                                <li v-for="(song,index) in item.songList" :key="index">
                                    <span class="num">{{song.number}}</span>
                                    <span class="song">{{song.songName}}</span> - 
                                    <span class="singer">{{song.singerName|fliterName}}</span>
                                </li>
                            </ul>
                        </div>
                    </div>
                </div>
            </router-link>
        </div>
   </div>
</template>

<script>
   export default {
       name:'',
       data(){
            return{
               data:[
                ],
                a:false,
                arr:"12345678"
            }
       },
        created(){

            this.$axios.get("https://music.niubishanshan.top/api/v2/music/toplist")
            .then((res)=>{
                this.data=res.data.data;
                
                this.a=true
                console.log(res.data)
            })
            .catch(function (error) {
                console.log(error);
            });
            this.arr.split("");
            console.log(this.arr)
        },
        filters:{
            fliterName(value){
                if(value.length>10){
                    return value.substring(0,5)+"..."
                }else{
                    return value
                } 
            },

            fliterNum(value){
                value=value+"";
                var arr=value.split("");
                var len=arr.length
                if(arr[len-4]>=5){
                    arr[len-5]++
                    arr[len-4]=0;
                }
                arr.splice(-4,0,".");
                arr.splice(-4,3);
                return arr.join("")+"ä¸‡";
            }
        }
   }
</script>

<style scoped>
    .count{
        display: inline-block;
        width: 100%;
        /* background: #000; */

        color: rgba(255, 255, 255, 0.589);
        bottom: 0;
        font-size: 12px;
        text-align: left;
        height: 30px;
        padding-left: 5px;
        box-sizing: border-box;
        line-height: 30px;
        left: 0;
        position: absolute;
    }
    #rank{
        width: 100%;
        background: #eee;
        padding-bottom: 50px;
    }
    .rankList{
        width: 100%;
        padding: 10px 10px 0 10px;
        box-sizing: border-box;
    }
    .rank-info{
        width: 100%;
        background: #ccc;
        display: flex;
    }
    .img{
        flex: 1;
        position: relative;
    }
    .img img{
        width: 100%;
    }
    /* .jp{
        width: 88.75px !important;
    } */
    .song-rank{
        flex: 3;
        display: flex;
        height: 100%;
        background: #fff;
    }
    .box{
        padding: 3px 5px 5px 10px;
        box-sizing: border-box;
        background: #fff
    }
    h3{
        font-size: 16px;
        font-weight: normal;
        margin-bottom: 5px;
    }
    .box ul li{
        font-size: 14px;
            word-break: break-all;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 1;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    }
    .box ul li span{
        overflow: hidden;
        text-overflow:ellipsis;
        white-space: nowrap;
    }
    .num,.singer{
        color: rgba(0,0,0,.5);
    }
    .num{
        margin-right: 7px;
    }
    .song{
        color: #000
    }
    
</style>